.footer {
  &-top {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 40px;
    padding: 55px 0 70px;
  }
  &__group {
    max-width: 315px;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    .footer__btn {
      display: none;
    }
  }
  &__phone {
    display: block;
    font-family: 'Vetrino';
    font-size: 30px;
    line-height: 100%;
    letter-spacing: -1.2px;
    margin-bottom: 10px;
    &:hover {
      color: #DD7500;
    }
    &-wrap {
      margin-bottom: 40px;
    }
    &-btn {
      background: transparent;
      padding: 0;
      font-size: 14px;
      line-height: 120%;
      letter-spacing: 0.84px;
      color: #FFF1DA;
      &:hover {
        color: #DD7500;
      }
    }
    &-text {
      display: none;
      font-size: 14px;
      line-height: 120%;
      letter-spacing: -0.28px;
      margin-bottom: 10px;
    }
  }
  &__location,
  &__time {
    font-size: 14px;
    line-height: 120%;
    letter-spacing: 0.84px;
  }
  &__location {
    margin-bottom: 12px;
  }
  &__time {
    margin-bottom: 40px;
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 40px;
  }
  &__social {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid rgba(255, 241, 218, 0.2);
    border-radius: 50%;
    padding: 10px;
    &:hover {
      background: #DD7500;
    }
    svg {
      width: auto;
      max-width: 100%;
      max-height: 100%;
      height: auto;
    }
  }
  &__logo {
    width: 100%;
    display: block;
    margin-top: auto;
    img {
      width: 100%;
    }
  }
  &__group2 {
    max-width: 435px;
  }
  &__btn {
    height: 54px;
    padding: 5px 42px;
    margin-bottom: 40px;
    span {
      font-size: 12px;
      letter-spacing: 1.2px;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-bottom: 40px;
    li a {
      font-size: 16px;
      line-height: 100%;
      letter-spacing: 1.6px;
      text-transform: uppercase;
      &:hover {
        color: #DD7500;
      }
    }
  }
  &__list2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    margin-bottom: 20px;
    li a {
      font-size: 11px;
      line-height: 100%;
      letter-spacing: 1.1px;
      text-transform: uppercase;
      &:hover {
        color: #DD7500;
      }
    }
  }
  &__info {
    font-size: 10px;
    line-height: 120%;
    font-weight: 300;
    letter-spacing: 0.6px;
    color: rgba(255, 241, 218, 0.6);
  }
  &__group3 {
    max-width: 335px;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    .footer__logo,
    .footer__info {
      display: none;
    }
  }
  &__form-title {
    font-family: 'Vetrino';
    font-size: 30px;
    line-height: 100%;
    letter-spacing: -1.2px;
    margin-bottom: 16px;
  }
  &__form-subtitle {
    font-size: 16px;
    line-height: 125%;
    font-weight: 300;
    letter-spacing: -0.32px;
    margin-bottom: 40px;
  }
  .form {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    margin-top: auto;
    &-group {
      input {
        height: 54px;
        border-color: rgba(255, 241, 218, 0.6);
        padding: 0 30px;
        color: #FFF1DA;
        &::placeholder {
          color: rgba(255, 241, 218, 0.6);
        }
      }
      .input-info__text {
        left: unset;
        right: 0;
        background: #fff1da;
        color: #1b1d25;
      }
    }
    &-btn {
      width: 100%;
      height: 54px;
      padding: 5px 15px;

      .btn-text {
        font-size: 12px;
      }
    }
    &-check {
      max-width: 306px;
      label {
        &:before {
          border-color: #FFF1DA;
        }
      }
    }
  }
  &-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    border-top: 1px solid rgba(255, 241, 218, 0.2);
    padding: 15px 0 30px;
  }
  &__group4 {
    flex-shrink: 0;
  }
  &__text {
    display: flex;
    align-items: start;
    gap: 40px;
    p {
      font-size: 10px;
      line-height: 120%;
      font-weight: 300;
      letter-spacing: 0.6px;
      text-transform: uppercase;
      color: rgba(255, 241, 218, 0.6);
    }
    p a {
      &:hover {
        color: #DD7500;
      }
    }
  }
  &__group5 {
    max-width: 880px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
  }
  &__logo2 {
    display: flex;
    align-items: center;
    gap: 8px;
    span {
      font-size: 10px;
      line-height: 120%;
      font-weight: 300;
      letter-spacing: 0.6px;
      text-transform: uppercase;
      color: rgba(255, 241, 218, 0.6);
    }
    i {
      width: 16px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &-top {
      flex-direction: column;
      align-items: start;
      gap: 50px;
      padding: 20px 0 15px;
    }
    &__group {
      width: 100%;
      max-width: 100%;
      .footer__logo {
        display: none;
      }
      .footer__btn {
        display: flex;
      }
    }
    &__phone {
      font-size: 24px;
      letter-spacing: -0.24px;
      &-wrap {
        margin-bottom: 30px;
      }
      &-text {
        display: block;
      }
      &-btn {
        font-size: 12px;
        letter-spacing: 0.72px;
      }
    }
    &__location,
    &__time {
      font-size: 12px;
      letter-spacing: 0.72px;
    }
    &__time {
      margin-bottom: 30px;
    }
    &__socials {
      margin-bottom: 30px;
    }
    &__logo {
      max-width: 335px;
    }
    &__group2 {
      max-width: 100%;
      display: none;
      .footer__btn {
        display: none;
      }
      .footer__info {
        display: none;
      }
    }
    &__btn {
      width: 100%;
      height: 42px;
      margin: 0;
      span {
        font-size: 10px;
        letter-spacing: 1px;
      }
    }
    &__group3 {
      width: 100%;
      max-width: 100%;
      .footer__logo {
        display: block;
        margin-bottom: 50px;
      }
      .footer__info {
        display: block;
      }
    }
    &__form-title {
      font-size: 24px;
      letter-spacing: -0.24px;
      margin-bottom: 16px;
    }
    &__form-subtitle {
      font-size: 12px;
      letter-spacing: 0.12px;
      margin-bottom: 30px;
    }
    .form {
      margin-bottom: 50px;
      &-group {
        input {
          height: 42px;
          font-size: 10px;
          letter-spacing: 1px;
        }
      }
      &-btn {
        height: 42px;
        span {
          font-size: 10px;
          letter-spacing: 1px;
        }
      }
    }
    &-bottom {
      position: relative;
      flex-direction: column;
      align-items: start;
      gap: 20px;
    }
    &__text {
      flex-direction: column;
      align-items: start;
      gap: 10px;
    }
    &__group5 {
      order: -1;
    }
    &__logo2 {
      position: absolute;
      right: 0;
      bottom: 24px;
    }
  }
}