.main {
  height: 100% !important;
  min-height: 100vh;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 210px 0 60px;
  color: #FFF1DA;
  transition: background-image 1.5s ease-in-out;
  &__title {
    max-width: 1250px;
    text-align: center;
    font-family: 'Vetrino';
    font-size: 80px;
    line-height: 100%;
    font-weight: 400;
    letter-spacing: 0.8px;
    margin-bottom: 40px;
    span {
      color: #DD7500;
    }
  }
  &__btn {
    height: 54px;
    text-align: start;
    gap: 10px;
    padding: 5px 12px 5px 78px;
    margin: 0 auto 100px auto;
    .btn-icon {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      max-width: 65px;
      img {
        max-width: 100%;
      }
    }
    .btn-text {
      font-size: 13px;
      letter-spacing: 1.3px;
    }
    .btn-play {
      position: relative;
      z-index: 2;
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #FFF1DA;
      border-radius: 50%;
      padding: 10px;
      img {
        max-width: 100%;
      }
    }
  }
  &__items {
    max-width: 830px;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 90px / 3));
    gap: 45px;
    padding: 0 30px;
    margin: 0 auto;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &-number {
      font-family: 'Vetrino';
      font-size: 20px;
      line-height: 100%;
      letter-spacing: -0.8px;
      margin-bottom: 15px;
      span {
        font-size: 56px;
        letter-spacing: 2.24px;
      }
    }
    &-text {
      font-size: 10px;
      line-height: 140%;
      letter-spacing: 1px;
      text-transform: uppercase;
    }
  }
  &__mob {
    display: none;
  }
}
.gallery {
  position: relative;
  padding: 230px 0 60px;
  .section-top {
    max-width: 580px;
  }
  &__images {
    position: relative;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    margin-top: 7.5em;
    @media screen and (min-width: 769px) {
      transform: scale(1.1);
      transition: all 1s;
      width: calc(100vw - (100vw - 100%));
      font-size: calc(100vw * (16 / 1920));
      @media screen and (max-width: 768px) {
        font-size: calc(100vw * (16 / 576));
      }
    }
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 5em;
      width: 100%;
      height: 17.813em;
      background: linear-gradient(0.00deg, rgb(99, 112, 87),rgba(99, 112, 87, 0) 100%);
      pointer-events: none;
    }
  }
  &__images-grid {
    width: calc(100% + 27.5em);
    display: grid;
    grid-template-columns: repeat(5, calc(100% / 5 - 5em / 5));
    gap: 1.25em;
    &:nth-child(odd) {
      margin-left: -1.875em;
    }
    &:nth-child(even) {
      margin-left: -9.75em;
    }
    &:last-child {
      height: calc(100% - 5em);
      overflow: hidden;
    }
  }
  &__img {
    position: relative;
    padding-bottom: 66%;
    opacity: 0;
    transition: all 1s;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 0.625em;
      transition: all 1s;
    }
  }
  @media screen and (min-width: 769px) {
    &__img--big {
      padding-bottom: 100%;
      opacity: 1;
      img {
        z-index: 2;
        transform: scale(1.73);
        transform-origin: right top;
        margin-left: 1.875em;
      }
      .gallery__img-desc {
        right: 3.125em;
      }
    }
    &__img--big2 {
      padding-bottom: 100%;
      opacity: 1;
      img {
        z-index: 2;
        transform: scale(1.73);
        transform-origin: left top;
        margin-left: 2.25em;
      }
      .gallery__img-desc {
        left: 3.75em;
      }
    }
  }
  &__img-desc {
    position: absolute;
    z-index: 2;
    top: 1.875em;
  }
  &__img-text {
    display: block;
    font-family: 'Vetrino';
    font-size: 1.875em;
    line-height: 100%;
    letter-spacing: -0.075em;
  }
  &__img-text2 {
    display: block;
    font-family: 'Vetrino';
    font-size: 7.5em;
    line-height: 100%;
    letter-spacing: 0.075em;
  }
  &__text {
    max-width: 425px;
    font-size: 24px;
    line-height: 125%;
    font-weight: 300;
    letter-spacing: -0.48px;
    margin-top: 20px;
    margin-left: auto;
    margin-right: 225px;
  }
  .music {
    display: flex;
    align-items: center;
    text-align: start;
    gap: 20px;
    background: transparent;
    padding: 0;
    color: #FFF1DA;
    margin-top: 110px;
    &-icon {
      width: 60px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 125%;
      font-weight: 300;
      letter-spacing: -0.32px;
    }
  }
  &__icon {
    position: absolute;
    top: 0;
    left: 0;
    max-width: 604px;
    pointer-events: none;
    img {
      max-width: 100%;
    }
  }
  &__icon2 {
    position: absolute;
    top: 0;
    right: 0;
    max-width: 855px;
    pointer-events: none;
    img {
      max-width: 100%;
    }
  }
}
.gallery--active {
  @media screen and (min-width: 769px) {
    .gallery__images {
      transform: scale(1);
    }
    .gallery__img {
      opacity: 1;
    }
    .gallery__img--big {
      padding-bottom: 66%;
      img {
        transform: scale(1);
        margin: 0 !important;
      }
    }
    .gallery__img--big2 {
      padding-bottom: 66%;
      img {
        transform: scale(1);
        margin: 0 !important;
      }
    }
    .gallery__img-desc {
      transform: scale(0.8);
      opacity: 0;
      pointer-events: none;
    }
  }
}
.area {
