.advantages {
  position: relative;
  padding: 150px 0 75px;
  .container {
    position: relative;
    z-index: 2;
  }
  .section-top {
    max-width: 566px;
  }
  &__img {
    position: absolute;
    top: -400px;
    right: 0;
    pointer-events: none;
    img {
      max-width: 100%;
    }
  }
  &__items {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
    margin-top: 100px;
  }
  &__item {
    position: relative;
    transform: translateY(-60px);
    min-height: 240px;
    width: 50%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
    border-top: 1px solid rgba(255, 241, 218, 0.2);
    border-bottom: 1px solid rgba(255, 241, 218, 0.2);
    padding-top: 15px;
    padding-bottom: 15px;
    margin-top: -1px;
    opacity: 0;
    &:before,
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);
      height: calc(100% - 50px);
      background: rgba(255, 241, 218, 0.2);
    }
    &:nth-child(1) {
      padding-right: 50px;
      margin-top: 0;
      &:after {
        right: 0;
        width: 1px;
      }
    }
    &:nth-child(2) {
      padding-left: 50px;
      padding-right: 50px;
      margin-top: 0;
      &:before {
        left: 0;
        width: 1px;
      }
    }
    &:nth-child(3) {
      width: 100%;
      max-width: 710px;
      padding-left: 50px;
      padding-right: 50px;
      &:before {
        left: 0;
        width: 1px;
      }
      &:after {
        right: 0;
        width: 1px;
      }
    }
    &:nth-child(4) {
      border-bottom: unset;
      padding-left: 145px;
      padding-right: 50px;
      &:before {
        left: 75px;
        width: 1px;
      }
      &:after {
        right: 0;
        width: 1px;
      }
    }
    &:nth-child(5) {
      border-bottom: unset;
      padding-left: 50px;
      padding-right: 50px;
      &:before {
        left: 0;
        width: 1px;
      }
    }
    &-number {
      font-family: 'Vetrino';
      font-size: 100px;
      line-height: 80%;
      letter-spacing: -4px;
      margin-bottom: 24px;
      span {
        font-size: 36px;
        line-height: 100%;
        letter-spacing: -1.2px;
      }
    }
    &-title {
      max-width: 175px;
      font-size: 13px;
      line-height: 125%;
      letter-spacing: 1.3px;
      text-transform: uppercase;
    }
    &-group2 {
      display: flex;
      align-items: center;
      gap: 20px;
    }
    &-icon {
      width: 40px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-text {
      max-width: 200px;
      font-size: 16px;
      line-height: 125%;
      font-weight: 300;
      letter-spacing: -0.32px;
      color: rgba(255, 241, 218, 0.6);
    }
  }
}
.map {
  position: relative;
  min-height: 100vh;
  padding: 75px 0 0 0;
  &--active {
    .map__text {
      transform: translateY(0);
      opacity: 1;
      pointer-events: auto;
    }
  }
  .section-top {
    max-width: 660px;
    margin-bottom: 50px;
    &__title {
      margin-bottom: 20px;
    }
    &__subtitle {
      margin: 0;
    }
  }
  &-images {
    position: relative;
    max-width: 62.5em;
    margin: 0 auto;
    width: calc(100vw - (100vw - 100%));
    font-size: calc(100vw * (16 / 1320));
    @media screen and (max-width: 1320px) {
      font-size: calc(100vw * (16 / 1320));
    }
    @media screen and (max-width: 992px) {
      font-size: calc(100vw * (16 / 768));
    }
    @media screen and (max-width: 576px) {
      font-size: calc(100vw * (16 / 360));
    }
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 6.813em;
      background: linear-gradient(0.00deg, rgb(99, 112, 87),rgba(99, 112, 87, 0) 46.33%);
      pointer-events: none;
    }
  }
  &-img {
    position: relative;
    padding-bottom: 75px;
    img {
      width: 100%;
    }
    .hint {
      position: absolute;
      display: flex;
      align-items: center;
      gap: 0.188em;
    }
    .hint--1 {
      top: 2.5em;
      left: 22.375em;
    }
    .hint--2 {
      top: 8.5em;
      left: 26.5em;
    }
    .hint--3 {
      top: 14.5em;
      left: 21em;
    }
    .hint--4 {
      top: 20.5em;
      left: 14.5em;
    }
    .hint--5 {
      top: 25.5em;
      left: 14.5em;
    }
    .hint--6 {
      top: 21.5em;
      left: 37em;
    }
    .hint--7 {
      top: 20em;
      left: 40.5em;
    }
    .hint-icon {
      width: 1.5em;
      height: 1.5em;
      flex-shrink: 0;
      background: #DD7500;
      border: 0.25em solid #FFF1DA;
      border-radius: 50%;
      &:hover {
        & ~ .hint-text {
          opacity: 1;
          pointer-events: auto;
        }
      }
    }
    .hint-text {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #FFF1DA;
      border-radius: 4px;
      padding: 0.25em 0.5em;
      font-size: 0.75em;
      line-height: 120%;
      letter-spacing: 0.047em;
      color: #1B1D25;
      opacity: 0;
      pointer-events: none;
      transition: all 0.125s;
    }
  }
  &-img2 {
    padding-bottom: 75px;
    img {
      position: absolute;
      top: -5.5em;
      right: -39.25em;
      width: 400%;
      display: none;
      opacity: 0.6;
      pointer-events: none;
    }
  }
  &__group {
    position: absolute;
    z-index: 3;
    right: max(30px, calc((100% - 1320px) / 2 ));
    bottom: 245px;
    max-width: 450px;
  }
  &__text {
    transform: translateY(-40px);
    font-size: 24px;
    line-height: 125%;
    font-weight: 300;
    letter-spacing: -0.48px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s;
  }
  &__btn {
    height: 44px;
    padding: 5px 42px;
    margin-top: 40px;
    span {
      font-size: 12px;
      letter-spacing: 1.2px;
      text-transform: uppercase;
    }
  }
  &__btn2 {
    position: absolute;
    z-index: 3;
    right: 24px;
    bottom: 160px;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    box-shadow: -2.4px 4.8px 7.2px 0px rgba(10, 11, 10, 0.15);
    svg {
      position: relative;
      z-index: 2;
      max-width: 80%;
      max-height: 100%;
      height: auto;
    }
  }
}
.house {
  position: relative;
  padding: 75px 0 600px;
  margin-bottom: 75px;
  overflow: hidden;
  .section-top {
    position: relative;
    z-index: 2;
    max-width: 650px;
    margin-bottom: 75px;
  }
  &__img {
    position: absolute;
    left: 50%;
