    height: 600px;
    transition: all 1s;
    img {
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 100%;
      height: 100%;
      border-radius: 16px;
    }
  }
  &__text {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    transform: translateY(-30px);
    max-width: 500px;
    padding: 60px;
    font-size: 24px;
    line-height: 125%;
    font-weight: 300;
    letter-spacing: -0.48px;
    opacity: 0;
    pointer-events: none;
    transition: all 1.5s;
  }
}
.house--active {
  .section-top {
    color: #000;
  }
  .house__img {
    width: 100%;
    max-width: 100%;
    height: 100%;
  }
  .house__text {
