    pointer-events: auto;
  }
}
.services {
  padding: 75px 0;
  .section-top {
    max-width: 940px;
    margin-bottom: 100px;
  }
  &__wrap {
    position: relative;
    max-width: 1190px;
    &:hover {
      .swiper-button-prev,
      .swiper-button-next {
        display: flex;
      }
    }
  }
  &__slider {
    max-width: 600px;
  }
  .swiper {
    overflow: unset;
  }
  .swiper-slide {
    transform: translateY(60px);
    max-width: 600px;
    width: 100% !important;
    opacity: 0;
    transition: all 0.125s;
    .service-card {
      max-width: 400px;
    }
    .service-card__desc {
      display: none;
    }
    &-active {
      .service-card {
        max-width: 100%;
        margin: 0;
      }
      .service-card__desc {
        display: block;
      }
    }
    &-next {
      .service-card {
        max-width: 500px;
        margin: 0;
      }
      .service-card__desc {
        display: block;
      }
      .service-card__price {
        display: none;
      }
      .service-card__btn {
        display: none;
      }
    }
    &-prev {
      .service-card {
        max-width: 500px;
        margin-left: auto;
      }
      .service-card__desc {
        display: block;
      }
      .service-card__price {
        display: none;
      }
      .service-card__btn {
        display: none;
      }
    }
    &-next + .swiper-slide {
      .service-card {
        margin-left: -100px;
      }
    }
    &-next + .swiper-slide + .swiper-slide {
      .service-card {
        margin-left: -300px;
      }
    }
    &-next + .swiper-slide + .swiper-slide ~ .swiper-slide {
      .service-card {
        margin-left: -500px;
      }
    }
    &-next + .swiper-slide + .swiper-slide ~ .swiper-slide + .swiper-slide {
      .service-card {
        margin-left: -700px;
      }
    }
    &-next + .swiper-slide + .swiper-slide ~ .swiper-slide + .swiper-slide + .swiper-slide {
      .service-card {
        margin-left: -900px;
      }
    }
    &-next + .swiper-slide + .swiper-slide ~ .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide {
      .service-card {
        margin-left: -1100px;
      }
    }
    &-next + .swiper-slide + .swiper-slide ~ .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide {
      .service-card {
        margin-left: -1300px;
      }
    }
  }
  .swiper-slide:has(+ .swiper-slide-prev) {
    .service-card {
      margin-left: auto;
      margin-right: -100px;
    }
  }
  .swiper-slide:has(+ .swiper-slide + .swiper-slide-prev) {
    .service-card {
      margin-left: auto;
      margin-right: -300px;
    }
  }
  .swiper-slide:has(+ .swiper-slide + .swiper-slide + .swiper-slide-prev) {
    .service-card {
      margin-left: auto;
      margin-right: -500px;
    }
  }
  .swiper-slide:has(+ .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide-prev) {
    .service-card {
      margin-left: auto;
      margin-right: -700px;
    }
  }
  .swiper-slide:has(+ .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide-prev) {
    .service-card {
      margin-left: auto;
      margin-right: -900px;
    }
  }
  .swiper-slide:has(+ .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide-prev) {
    .service-card {
      margin-left: auto;
      margin-right: -1100px;
    }
  }
  .swiper-slide:has(+ .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide-prev) {
    .service-card {
      margin-left: auto;
      margin-right: -1300px;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    top: 40%;
    width: 120px;
    height: 120px;
    display: none;
    justify-content: center;
    align-items: center;
    background: #FFF1DA;
    border-radius: 50%;
    opacity: 1 !important;
    &:after {
      display: none;
    }
    svg {
      max-width: 80%;
      max-height: 100%;
    }
  }
  .swiper-button-prev {
    left: -25px;
  }
  .swiper-button-next {
    right: -25px;
  }
  .swiper-button-disabled {
    display: none !important;
  }
}
