.plan {
  position: relative;
  min-height: 48.75em;
  max-height: 48.75em;
  background-image: url("../img/plan-img.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 3.125em 0 5.625em;
  width: calc(100vw - (100vw - 100%));
  font-size: calc(100vw*(16 / 1320));
  @media screen and (max-width: 576px) {
    font-size: calc(100vw*(16 / 375));
  }
  &__img {
    position: absolute;
    z-index: 2;
    top: 3em;
    left: 7.6em;
    width: 54.063em;
    svg {
      position: relative;
      width: 100%;
      height: auto;
    }
  }
  .path-btn {
    position: relative;
    z-index: 4;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      fill: rgba(255, 255, 255, 0.5);
    }
  }
  .service-card {
    position: absolute;
    z-index: 6;
    max-width: 300px;
    display: none;
    &__img {
      padding-bottom: 0;
      img {
        position: static;
        height: auto;
      }
    }
    &__desc {
      padding: 20px 16px;
    }
    &__items {
      gap: 24px;
      border-bottom: 1px solid rgba(27, 29, 37, 0.1);
      padding-bottom: 16px;
      margin-top: 0;
    }
    &__item {
      &:not(:last-child) {
        padding-right: 24px;
      }
      &-number {
        font-size: 30px;
        letter-spacing: -1.2px;
      }
      &-text {
        font-size: 10px;
        letter-spacing: 0.6px;
      }
    }
    &__price {
      font-size: 30px;
      letter-spacing: -1.2px;
      margin-top: 16px;
      span {
        font-size: 30px;
        letter-spacing: -1.2px;
        margin-top: 16px;
      }
    }
  }
  .plan-card2 {
    .service-card__number {
      position: static;
      background: #637057;
      border-radius: 100px;
      padding: 5px 12px;
    }
    .service-card__items {
      margin-top: 24px;
    }
    .form {
      display: grid;
      grid-template-columns: 100%;
      gap: 8px;
      margin-top: 24px;
      &-group {
        input {
          height: 44px;
          padding: 0 30px;
        }
      }
      &-btn {
        margin-top: 0;
      }
      &-check {
        label {
          color: #1B1D25;
        }
      }
    }
  }
  &__btn {
    position: absolute;
    z-index: 5;
    width: 2.25em;
    height: 2.25em;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.3s;
    img {
      min-width: 1.125em;
      max-width: 80%;
    }
    &--1 {
      top: 2.5em;
      left: 32em;
    }
    &--2 {
      top: 7.5em;
      left: 41em;
    }
    &--3 {
      right: 16em;
      bottom: 16em;
    }
    &--4 {
      left: 33.5em;
      bottom: 16em;
    }
    &--5 {
      left: 20em;
      bottom: 16.5em;
    }
    &--6 {
      left: 23em;
      bottom: 14em;
    }
    &--7 {
      left: 20em;
      bottom: 12em;
    }
    &--8 {
      left: 18em;
      bottom: 14em;
    }
    .circle {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 2.875em;
      height: 2.875em;
      border: 0.5px solid #fff;
      border-radius: 50%;
      pointer-events: none;
    }
    .circle2 {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 3.625em;
      height: 3.625em;
      border: 0.5px solid rgba(255, 255, 255, 0.6);
      border-radius: 50%;
      pointer-events: none;
    }
    .circle3 {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 4.5em;
      height: 4.5em;
      border: 0.5px solid rgba(255, 255, 255, 0.15);
      border-radius: 50%;
      pointer-events: none;
    }
  }
  &__images {
    position: absolute;
    z-index: 12;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &--active {
      opacity: 1;
      pointer-events: auto;
    }
    .swiper-wrapper {
      align-items: stretch;
    }
    .swiper-slide {
      height: auto;
      img {
        width: 100%;
        height: 100%;
        border-radius: 1em;
      }
    }
    .swiper-slide-thumb-active {
      img {
        border: 0.188em solid #FFF1DA;
      }
    }
  }
  &__images-big {
    height: 100%;
  }
  &__images-wrap {
    position: absolute;
    z-index: 2;
    left: 50%;
    bottom: 1.875em;
    transform: translate(-50%, 0);
    max-width: 20.5em;
    width: 100%;
    margin: 0 auto;
  }
  &__images-small {
    .swiper-slide {
      position: relative;
      width: calc(25% - 1em) !important;
      border-radius: 0.375em;
      padding-bottom: 20%;
      margin-right: 1em !important;
      cursor: pointer;
      img {
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
  &__images-close {
    position: absolute;
    z-index: 2;
    top: 1.875em;
    right: 1.875em;
    width: 2em;
    height: 2em;
    min-width: 32px;
    min-height: 32px;
    background: #fff;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.3s;
    svg {
      max-width: 80%;
      width: auto;
      min-width: 1em;
      height: auto;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    top: 40%;
    width: 7.5em;
    height: 7.5em;
    justify-content: center;
    align-items: center;
    background: #FFF1DA;
    border-radius: 50%;
    opacity: 1 !important;
    &:after {
      display: none;
    }
    svg {
      max-width: 80%;
      max-height: 100%;
    }
  }
  .swiper-button-prev {
    left: 1.875em;
  }
  .swiper-button-next {
    right: 1.875em;
  }
  .swiper-button-disabled {
    display: none !important;
  }
}

@media screen and (max-width: 576px) {
  .plan {
    min-height: 43.75em;
    max-height: 43.75em;
    background-image: url("../img/plan-img--mob.webp");
    padding: 40px 0;
    &__img {
      top: 13.8em;
      left: 0.4em;
      width: 20.188em;
    }
    &__btn {
      width: 1.25em;
      height: 1.25em;
      &--1 {
        top: 11em;
        left: 12.5em;
      }
      &--2 {
        top: 14em;
        left: 17em;
      }
      img {
        max-width: 0.75em;
      }
      .circle {
        width: 2em;
        height: 2em;
      }
      .circle2 {
        width: 3em;
        height: 3em;
      }
      .circle3 {
        width: 4em;
        height: 4em;
      }
    }
    .service-card {
      top: unset !important;
      left: 0 !important;
      right: unset !important;
      bottom: 0 !important;
      max-width: 100%;
      width: 100%;
      border-radius: 16px 16px 0 0;
      &__desc {
        padding: 24px 20px 20px 20px;
      }
      &__item {
        &-number {
          font-size: 24px;
          letter-spacing: 0.24px;
        }
        &-text {
          font-size: 10px;
          letter-spacing: 0.1px;
        }
      }
      &__price {
        font-size: 18px;
        letter-spacing: -0.18px;
        span {
          font-size: 24px;
          letter-spacing: 0.24px;
        }
      }
    }
    &__images {
      &-close {
        top: 1.25em;
        right: 1.25em;
      }
    }
    .swiper-button-prev {
      left: 1em;
      width: 3em;
      height: 3em;
    }
    .swiper-button-next {
      right: 1em;
      width: 3em;
      height: 3em;
    }
  }
}