.wrapper {
  overflow: hidden;
}
.btn-text {
  position: relative;
  z-index: 2;
}
.btn-orange {
  position: relative;
  background: #DD7500;
  border-radius: 100px;
  color: #FFF1DA;
  .btn-text {
    line-height: 100%;
    text-transform: uppercase;
  }
  .circle {
    background: #BD6400;
  }
}
.btn-brown {
  position: relative;
  background: rgba(221, 117, 0, 0.2);
  border-radius: 100px;
  color: #1B1D25;
  &:hover {
    color: #FFF1DA;
  }
  .btn-text {
    line-height: 100%;
    text-transform: uppercase;
  }
  .circle {
    background: #DD7500;
  }
}
.btn-pink {
  position: relative;
  background: #FFF1DA;
  border-radius: 100px;
  color: #1B1D25;
  .btn-text {
    line-height: 100%;
    text-transform: uppercase;
  }
  .circle {
    background: #FFF7EA;
  }
}
.btn-animation {
  position: relative;
  overflow: hidden;
  &:hover {
    .circle {
      width: 200%;
      height: 400%;
    }
  }
  .circle {
    position: absolute;
    z-index: 0;
    display: block;
    width: 0;
    height: 0;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    transition: width 0.7s, height 0.7s;
  }
}
.form {
  &-group {
    position: relative;
    input {
      width: 100%;
      background: transparent;
      border: 1px solid rgba(27, 29, 37, 0.3);
      border-radius: 100px;
      font-size: 12px;
      line-height: 100%;
      letter-spacing: 1.2px;
      text-transform: uppercase;
      color: #1B1D25;
    }
    .input-info {
      position: absolute;
      top: 50%;
      right: 20px;
      transform: translate(0, -50%);
      opacity: 0;
      pointer-events: none;
      transition: all 0.125s;
      &:hover {
        .input-info__text {
          opacity: 1;
          pointer-events: auto;
        }
      }
      &__icon {
        width: 16px;
        cursor: pointer;
        img {
          width: 100%;
        }
      }
      &__text {
        position: absolute;
        left: 0;
        bottom: 100%;
        width: max-content;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #fff;
        border-radius: 15px;
        padding: 4px 12px;
        font-size: 12px;
        line-height: 120%;
        opacity: 0;
        pointer-events: none;
        transition: all 0.125s;
      }
    }
  }
  &-group--active {
    input {
      border-color: #CA3011;
      color: #CA3011;
      &::placeholder {
        color: #CA3011;
      }
    }
    .input-info {
      opacity: 1;
      pointer-events: auto;
    }
  }
  &-check {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
    }
    input:checked ~ label:before {
      background-image: url("../img/check.svg");
    }
    label {
      position: relative;
      display: block;
      padding-left: 24px;
      font-size: 10px;
      line-height: 120%;
      font-weight: 300;
      letter-spacing: 0.6px;
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 14px;
        height: 14px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        border: 1px solid #1B1D25;
        border-radius: 4px;
      }
      a {
        position: relative;
        z-index: 3;
      }
    }
  }
}
.section-top {
  text-align: center;
  margin: 0 auto;
  &__subtitle {
    font-size: 13px;
    line-height: 150%;
    text-transform: uppercase;
    margin-bottom: 30px;
  }
}
.h2 {
  font-size: 63px;
  line-height: 100%;
  font-weight: 200;
  letter-spacing: -3.15px;
}
.up {
  position: fixed;
  z-index: 5;
  right: 15px;
  bottom: 60px;
  width: 60px;
  height: 60px;
  background: rgba(255, 241, 218, 0.2);
  backdrop-filter: blur(12px);
  border-radius: 50%;
  opacity: 0;
  pointer-events: none;
  transition: all 0.125s;
  &--active {
    opacity: 1;
    pointer-events: auto;
  }
  img {
    max-width: 80%;
    max-height: 100%;
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(99, 112, 87, 0.5);
  padding: 15px 0;
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: calc(100% - 40px);
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
    }
  }
  &__close {
    position: absolute;
    top: -20px;
    right: -40px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    svg {
      position: relative;
      z-index: 2;
      max-width: 80%;
      max-height: 100%;
    }
  }
}
.modal--1 {
  color: #1B1D25;
  .modal__dialog {
    max-width: 1065px;
  }
  .modal__content {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    background: #fff;
    border-radius: 30px;
  }
  .modal__img {
    width: 330px;
    flex-shrink: 0;
    img {
      width: 100%;
      height: 100%;
      border-radius: 16px;
    }
  }
  .modal__desc {
    width: calc(100% - 330px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 40px;
  }
  .modal__title {
    font-size: 30px;
    line-height: 100%;
    letter-spacing: -0.6x;
    margin-bottom: 24px;
  }
  .modal__text {
    max-width: 375px;
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    margin-bottom: 24px;
    p {
      font-size: 16px;
      line-height: 125%;
      letter-spacing: -0.32px;
    }
  }
  .form {
    margin-top: auto;
    &-grid {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 12px / 2));
      gap: 12px;
      margin-bottom: 12px;
    }
    &-group {
      input {
        height: 54px;
        padding: 0 30px;
      }
    }
    &-btn {
      height: 60px;
      padding: 5px 15px;
      span {
        font-size: 12px;
        letter-spacing: 1.2px;
      }
    }
    &-check {
      max-width: 360px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .h2 {
    font-size: 48px;
    letter-spacing: -0.96px;
  }
}

@media screen and (max-width: 1200px) {
  .modal {
    &__close {
      top: -40px;
      right: 0;
    }
  }
}

@media screen and (max-width: 992px) {
  .section-top {
    &__subtitle {
      font-size: 10px;
      margin-bottom: 20px;
    }
  }
  .h2 {
    font-size: 26px;
    letter-spacing: -0.52px;
  }
  .up {
    right: 10px;
    width: 50px;
    height: 50px;
  }
  .modal--1 {
    .modal__img {
      width: 250px;
    }
    .modal__desc {
      width: calc(100% - 250px);
      padding: 24px;
    }
    .modal__title {
      font-size: 24px;
      margin-bottom: 16px;
    }
    .modal__text {
      gap: 8px;
      margin-bottom: 16px;
      p {
        font-size: 14px;
      }
    }
    .form {
      &-grid {
        margin-bottom: 8px;
      }
      &-group {
        input {
          height: 42px;
          font-size: 10px;
          letter-spacing: 1px;
        }
      }
      &-btn {
        height: 42px;
        span {
          font-size: 10px;
          letter-spacing: 1px;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .modal--1 {
    .form {
      &-grid {
        grid-template-columns: 100%;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .modal--1 {
    .modal__content {
      flex-direction: column;
    }
    .modal__img {
      width: 100%;
    }
    .modal__desc {
      width: 100%;
    }
  }
}

